___CSS_LOADER_EXPORT___.push([module.id, `/* Base (Mobile First) */
.hero-section {
    position: relative;
    width: 100%;
    height: 95vh; /* Reduce height for mobile */
    display: flex;
    justify-content: center;
    align-items: flex-start;
    background-color: #f0f0f0;
  }
  .hero-section::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to right, rgba(0, 0, 0, 0.5), transparent, rgba(0, 0, 0, 0.5));
    z-index: 1;
    pointer-events: none;
  }
  
  
  
  
  .hero-section img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: fit-content;
    object-fit: cover;
    object-position: center center; /* Center the image */
  }
  
  .slogan {
    font-size: 1.25rem;
    font-weight: bold;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);

    letter-spacing: 1.5px;
    z-index: 2;
  }
  

  .subslogan {

    font-weight: bold;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);

    letter-spacing: 1.5px;
    z-index: 2;
  }
  
  .hero-paragraph {
    font-size: 1.2rem;
    line-height: 1.6;
    margin: 50px 0px;
    color: rgb(255, 255, 255);
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
    z-index: 2;
  }

  
  .cta-button {
    display: inline-block;
    padding: 12px 24px;
    font-size: 1rem;
    font-weight: bold;
    color: white;
    background-color: #5ba88f;
    border-radius: 25px;
    text-decoration: none;
    transition: background-color 0.3s;
    z-index: 2;
  }
  
  .cta-button:hover {
    background-color: #4a8f78;
  }

  /* Tablet (From 481px up to 768px) */
  @media (min-width: 481px) and (max-width: 768px) {
    .hero-section {
      height: 75vh; /* Slightly larger for tablets */
    }
    
    .company-name {
      font-size: 2.5rem;
    }
  }
  
  /* Desktop */
  @media (min-width: 769px) {
    .hero-section {
      height: 100vh; /* Full height for desktops */
    }
    
    .company-name {
      font-size: 3rem;
    }
  }
  
  .hero-content {
    margin: 30px 20px;
    position: relative;
    text-align: center;
    color: white;
    font-family: Arial, sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;   
    min-height: 95vh; 
  }
  /* Content Style */
  .hero-content-title {


    margin: 0px;
    position: relative;
    text-align: center;
    color: white;
    font-family: Arial, sans-serif;
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  
  .company-name {
    font-size: 0.7rem;
    font-weight: lighter;
    letter-spacing: 1.5px;
    margin-bottom: 0 12px;
  }
  

.line {
  width: 60px;
  height: 1px;
  background-color: rgba(255, 255, 255, 0.5);
  margin: 20px;
}`, "",{"version":3,"sources":["webpack://./src/sections/HeroSection.css"],"names":[],"mappings":"AAAA,wBAAwB;AACxB;IACI,kBAAkB;IAClB,WAAW;IACX,YAAY,EAAE,6BAA6B;IAC3C,aAAa;IACb,uBAAuB;IACvB,uBAAuB;IACvB,yBAAyB;EAC3B;EACA;IACE,WAAW;IACX,kBAAkB;IAClB,MAAM;IACN,OAAO;IACP,WAAW;IACX,YAAY;IACZ,0FAA0F;IAC1F,UAAU;IACV,oBAAoB;EACtB;;;;;EAKA;IACE,kBAAkB;IAClB,MAAM;IACN,OAAO;IACP,WAAW;IACX,mBAAmB;IACnB,iBAAiB;IACjB,8BAA8B,EAAE,qBAAqB;EACvD;;EAEA;IACE,kBAAkB;IAClB,iBAAiB;IACjB,2CAA2C;;IAE3C,qBAAqB;IACrB,UAAU;EACZ;;;EAGA;;IAEE,iBAAiB;IACjB,2CAA2C;;IAE3C,qBAAqB;IACrB,UAAU;EACZ;;EAEA;IACE,iBAAiB;IACjB,gBAAgB;IAChB,gBAAgB;IAChB,yBAAyB;IACzB,2CAA2C;IAC3C,UAAU;EACZ;;;EAGA;IACE,qBAAqB;IACrB,kBAAkB;IAClB,eAAe;IACf,iBAAiB;IACjB,YAAY;IACZ,yBAAyB;IACzB,mBAAmB;IACnB,qBAAqB;IACrB,iCAAiC;IACjC,UAAU;EACZ;;EAEA;IACE,yBAAyB;EAC3B;;EAEA,oCAAoC;EACpC;IACE;MACE,YAAY,EAAE,gCAAgC;IAChD;;IAEA;MACE,iBAAiB;IACnB;EACF;;EAEA,YAAY;EACZ;IACE;MACE,aAAa,EAAE,6BAA6B;IAC9C;;IAEA;MACE,eAAe;IACjB;EACF;;EAEA;IACE,iBAAiB;IACjB,kBAAkB;IAClB,kBAAkB;IAClB,YAAY;IACZ,8BAA8B;IAC9B,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,gBAAgB;EAClB;EACA,kBAAkB;EAClB;;;IAGE,WAAW;IACX,kBAAkB;IAClB,kBAAkB;IAClB,YAAY;IACZ,8BAA8B;IAC9B,aAAa;IACb,mBAAmB;IACnB,mBAAmB;EACrB;;EAEA;IACE,iBAAiB;IACjB,oBAAoB;IACpB,qBAAqB;IACrB,qBAAqB;EACvB;;;AAGF;EACE,WAAW;EACX,WAAW;EACX,0CAA0C;EAC1C,YAAY;AACd","sourcesContent":["/* Base (Mobile First) */\r\n.hero-section {\r\n    position: relative;\r\n    width: 100%;\r\n    height: 95vh; /* Reduce height for mobile */\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: flex-start;\r\n    background-color: #f0f0f0;\r\n  }\r\n  .hero-section::before {\r\n    content: \"\";\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    width: 100%;\r\n    height: 100%;\r\n    background: linear-gradient(to right, rgba(0, 0, 0, 0.5), transparent, rgba(0, 0, 0, 0.5));\r\n    z-index: 1;\r\n    pointer-events: none;\r\n  }\r\n  \r\n  \r\n  \r\n  \r\n  .hero-section img {\r\n    position: absolute;